@import 'variables';
@import 'reset';

// Header

.header {
    background-color: $bg-color;

    &-top {
        background-color: $secondary-bg-color;
        color: $light-color;

        @include mobile {
          display: none; }


        &-container {
            @include center-content;
            padding: .8rem 0;

            span {
                margin-right: 1rem; }

            i {
                margin-right: .3rem; }

            a {
                color: $light-color; } } } }

.main-header {

    &-container {
        @include center-content;
        padding: 1.3rem 0;

        @include mobile {
          flex-direction: column;
          align-items: center; }

        a, span, i {
            color: $light-color; }

        #brand {
            font-weight: 900;
            font-size: 3rem;

            i {
                color: $primary-color;
                font-size: 2rem; } }

        #search-form {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 45%;

            @include mobile {
              width: 100%;
              margin: 2rem 0; }

            input {
                height: 40px; }

            #search {
                @include rounded-input;
                width: 60%; } }

        .header-actions-menu {
            display: flex;
            align-items: center;

            .wishlist-container, .header-cart-container {
                @include header-icon;

                @include mobile {
                  margin: 1rem; } } } } }

.header-bottom {
    background-color: $light-color;
    border-bottom: 2px solid $section-bg-color;
    border-top: 3px solid $primary-color;

    ul {
        @include center-content;
        justify-content: left;
        padding: 1rem 0;

        @include mobile {
          display: none; }

        li {
            margin-right: 1.3rem;

            a {
                color: $link-color;
                border-bottom: 2px solid transparent;

                &:hover {
                    color: $primary-color;
                    border-color: $primary-color; } } } } }

.banners {
    @include center-content;
    padding: 3rem 0;

    @include mobile {
      flex-direction: column;
      align-items: center; }


    .banner {
        width: 30%;
        height: 200px;
        position: relative;
        color: $light-color;
        transition: .4s;
        @include center-bg;
        background-size: 100%;

        @include mobile {
          width: 90%;
          margin: 1rem; }

        &:hover {
            background-size: 120%; }

        &-cover {
            background-color: $primary-color;
            width: 70%;
            height: 100%;
            opacity: .8;
            border-bottom-right-radius: 6rem; }

        &-content {
            position: absolute;
            top: 0;
            left: 0;
            padding: 2rem; }

        h2 {
            font-size: 2.1rem;
            margin-bottom: 1rem;
            max-width: 70%; }

        a {
            font-size: 1.5rem;
            color: $light-color;
            font-weight: bold;
            display: flex;
            align-items: center; }

        i {
            color: $light-color;
            margin-left: .8rem; } } }


#banner-1 {
    background-image: url("../images/banner-1.jpg"); }

#banner-2 {
    background-image: url("../images/banner-2.jpg"); }

#banner-3 {
    background-image: url("../images/banner-3.jpg"); }

.products-grid {
    @include center-content;
    flex-direction: column;
    padding: 2rem 0;

    h2 {
        font-size: 2rem;
        text-transform: uppercase;

        @include mobile {
          text-align: center; } }

    &-container {
        display: flex;
        justify-content: space-between;
        padding: 2rem 0;

        @include mobile {
          flex-direction: column;
          align-items: center; } }

    &-card {
        border: 1px solid $gray-text-color;
        position: relative;
        width: 20%;
        padding: 1rem;
        transition: .4s;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include mobile {
          width: 90%;
          margin: 1rem; }

        &:hover {
            border-color: $primary-color; }

        .label {
            text-transform: uppercase;
            border: 1px solid $primary-color;
            color: $primary-color;
            position: absolute;
            top: 1rem;
            right: 1rem;
            padding: 2px 6px; }

        .new {
            background-color: $primary-color;
            color: $light-color; }

        img {
            width: 100%; }

        .category, .product-name, .product-price, .rating-box, .btn {
            margin-bottom: 1rem; }

        .category {
            margin-top: 1rem;
            text-transform: uppercase;
            color: $gray-text-color; }

        .product-name {
            text-transform: uppercase;
            font-size: 1.2rem; }

        .product-price {
            color: $primary-color;
            font-size: 1.5rem;
            font-weight: bold; } } }

.deal-container {
    background-image: url("../images/deal_headset.png");
    background-repeat: no-repeat;
    background-position-x: 95%;
    background-position-y: 2rem;
    background-size: 25%;
    background-color: $section-bg-color;
    height: 550px;
    padding: 2rem;

    @include mobile {
      background-size: 70%;
      background-position-y: 130%;
      background-position-x: 120%;
      height: 700px; }

    &-content {
        height: 100%;
        flex-direction: column;
        align-items: center;
        @include center-content;
        justify-content: center;

        @include mobile {
          text-align: center; }

        &-timer {
            display: flex;
            margin-bottom: 2rem;

            @include mobile {
              flex-wrap: wrap;
              align-self: center;
              justify-content: center; } }

        .deal-container-timer {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: $primary-color;
            color: $light-color;
            margin: 0 1rem;

            @include mobile {
              margin-bottom: 1.5rem; }

            &-time {
                font-weight: bold;
                font-size: 1.5rem; } }

        &-title, &-subtitle {
            margin-bottom: 1rem;
            text-transform: uppercase;
            font-size: 2rem; }

        &-subtitle {
            margin-bottom: 2rem;
            font-size: 1.5rem; } } }

.newsletter-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
    border-top: 2px solid $section-bg-color;

    @include mobile {
      text-align: center; }


    h2 {
        font-size: 2rem;
        color: $dark-color;
        margin-bottom: 2rem;

        span {
            font-size: 2rem;
            font-weight: bold;
            text-transform: uppercase; } }

    form {
        display: flex;
        margin-bottom: 3rem;

        @include mobile {
          width: 100%; }

        input[type="email"] {
            border: 1px solid $section-bg-color;
            @include rounded-input;

            @include mobile {
              width: 100%; } } }

    .social-media {

        i {
            padding: 12px;
            margin: 5px;
            border: 1px solid $section-bg-color;
            cursor: pointer;
            color: $dark-color;
            transition: .4s;

            &:hover {
                color: $primary-color;
                background-color: $section-bg-color; } } } }

.footer {
    background-color: $bg-color;
    padding: 3rem 0;

    @include mobile {
      padding: 3rem; }

    a,p {
        color: $gray-text-color; }

    li,p {
        margin-bottom: 1rem;

        a {
            &:hover {
                color: $primary-color; } } }

    i {
        margin-right: .3rem; }

    h3 {
        color: $light-color;
        text-transform: uppercase;
        font-size: 1.75rem;
        margin-bottom: 1.5rem; }

    &-top {
        @include center-content;
        padding-bottom: 3rem;

        @include mobile {
          flex-direction: column; }

        &-about, &-categories, &-information, &-menu {
            max-width: 25%;

            @include mobile {
              max-width: 100%;
              margin-bottom: 1rem;
              text-align: center; } } }


    &-bottom {
        @include center-content;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &-payments {
            margin-bottom: 2rem;

            i {
                color: $dark-color;
                cursor: pointer;
                font-size: 3rem;
                margin: 5px;
                transition: .4s;

                &:hover {
                    color: $primary-color; } } } } }






























